
.timer-val {
  max-width: calc(100vw - 60px);
  max-height: calc(100vw - 60px);
  width: 85px;
  height: 25px;
  border-radius: 1px;
  object-fit: cover;
  display: flex;
  border: thin dashed #c2c2c2;
  background: #fcfcfc;
}

.timer-container2 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: thin dashed #c2c2c2;
  background: #fcfcfc;
  max-width: calc(100vw - 40px);
  max-height: calc(100vw - 40px);
  width: 85px;
  height: 25px;
  padding: 5px;
  border-radius: 1px;
  position: relative;
}

.timer-container {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  max-width: calc(100vw - 40px);
  max-height: calc(100vw - 40px);
  width: 80px;
  height: 468px;
  padding: 10px;
  border-radius: 5px;
  position: relative;
}

.timer-section {
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}


